import React from 'react'
import s from './main.module.scss'
import {NavLink} from "react-router-dom";

export const Main = () => {
    return (
        <div className={s.main}>
            <div className={s.overlay}></div>
            <div className={s.container}>
                <div className={s.main__wrapper}>
                    <div className={s.main__title}>   </div>
                    <ul className={s.list}>
                        <li className={s.list__element}>
                            <NavLink to='/'></NavLink>
                        </li>
                        <li className={s.list__element}>
                            <NavLink to='/'></NavLink>
                        </li>
                        <li className={s.list__element}>
                            <NavLink to='/'></NavLink>
                        </li>
                        <li className={s.list__element}>
                            <NavLink to='/'></NavLink>
                        </li>
                        <li className={s.list__element}>
                            <NavLink to='/'></NavLink>
                        </li>
                    </ul>
                    <div className={s.filter}>
                        <div className={s.filter__item}>
                            <button className={s.filter__button} title='    '>
                                    
                            </button>
                        </div>
                    </div>
                    <div className={s.search__buttons}>
                        <div className={s.search__button}>
                            <NavLink className={s.button + " " + s.light__button} to='flat'>  </NavLink>
                        </div>
                        <div className={s.search__button}>
                            <NavLink className={s.button} to='flat'></NavLink>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}